*,
*:before,
*:after {
  box-sizing: border-box;
}
body {
  background: #ffdf8c;
  margin: 0;
}
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
button {
  border: none;
  background: transparent;
}
ul {
  margin: 0;
  padding: 0;
}
.AppContainer {
  width: 1430px;
  padding: 15px;
  margin: 0 auto;
}
.AppInner {
  width: 100%;
  background-color: #edf0f5;
  min-height: 800px;
  border-radius: 50px;
  margin-top: 50px;
  display: flex;
  justify-content: center;
}
